.fog-circle {
  position: fixed;
  display: block;
  aspect-ratio: 1/1;
  border: 2px solid #333;
  border-radius: 50%;
  z-index: -1;
}

.darkMode .fog-circle {
  border: 0;
  z-index: 0;
}

.fog-circle-one {
  left: 10%;
  top: 60%;
  width: 30%;
}

.darkMode .fog-circle-one {
  background: linear-gradient(180deg, #ffb31e 0%, #ff2e00 100%);
}

.fog-circle-two {
  left: 45%;
  top: 5%;
  width: 22%;
}

.darkMode .fog-circle-two {
  background: linear-gradient(180deg, #ff3b3b 0%, #7000ff 100%);
}

.fog-circle-three {
  left: 70%;
  top: 50%;
  width: 26%;
}

.darkMode .fog-circle-three {
  background: linear-gradient(180deg, #0275a6 0%, #00ff0a 100%);
}
